(function () {
  const calrendarSpec = {
    dayList: {
      ko: ['ì¼', 'ì›”', 'í™”', 'ìˆ˜', 'ëª©', 'ê¸ˆ', 'í† '],
      en: [
        'Sunday',
        'Monday',
        'Tuesday',
        'Wednesday',
        'Thursday',
        'Friday',
        'Saturday',
      ],
    },
    monthList: {
      ko: [
        '1ì›”',
        '2ì›”',
        '3ì›”',
        '4ì›”',
        '5ì›”',
        '6ì›”',
        '7ì›”',
        '8ì›”',
        '9ì›”',
        '10ì›”',
        '11ì›”',
        '12ì›”',
      ],
      en: [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December',
      ],
    },
    leapYear: [31, 29, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
    commonYear: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
  };
  // options = {
  //      date : new Date(),
  //      lang : 'ko'
  // }
  class CalrendarTodo {
    constructor(options) {
      const userOptions = {
        ...{
          date: new Date(),
          lang: 'ko',
        },
        options,
      };

      if (!userOptions.tbody) {
        console.error('tbodyë¥¼ ìž…ë ¥í•˜ì„¸ìš”.');
      }

      this._date = userOptions.date;
      this._dayList = calrendarSpec.dayList[userOptions.lang];
      this._monthList = calrendarSpec.monthList[userOptions.lang];
      this._lastDays = this.isLeapYear(this._date)
        ? calrendarSpec.leapYear
        : calrendarSpec.commonYear;
      this._lang = userOptions.lang;

      console.log(this);
    }

    //   getter, setter aear
    get date() {
      return this._date;
    }
    set date(date) {
      //   date ì„¤ì •ì— ë”°ë¥¸ ìœ¤ë…„ ê³„ì‚°
      this._date = date;
      this._lastDays = this.isLeapYear(this._date)
        ? calrendarSpec.leapYear
        : calrendarSpec.commonYear;

      this.renderer();
    }
    get lastDays() {
      return this._lastDays;
    }
    //   getter, setter aear

    isLeapYear(date) {
      const year =
        (typeof date === 'object' && date.getFullYear()) || date.getFullYear();

      //  1. 4ë¡œ ë‚˜ëˆ„ì–´ ë–¨ì–´ì§€ê³ , 100ìœ¼ë¡œ ë‚˜ëˆ„ì–´ ë–¨ì–´ì§€ì§€ ì•Šìœ¼ë©´ ìœ¤ë…„
      //  2. 4, 100, 400 ìœ¼ë¡œ ë‚˜ëˆ„ì–´ ë–¨ì–´ì§€ë©´ ìœ¤ë…„
      if ((0 === year % 4 && 0 !== year % 100) || 0 === year % 400) {
        return true;
      } else {
        return false;
      }
    }

    renderer() {
      const date = this.date;
      const firstDay = date.getDay();
      const lastDay = this.lastDays[date.getMonth()];

      let day = 1;

      document.getElementsByClassName('calendar-body')[0].innerHTML = '';

      const weeks = [...new Array(5)]
        .map((v, i) => i)
        .reduce((acc, cur, idx) => {
          const week = document.createElement('tr');

          for (let i = 0; i < 7; i++) {
            const td = document.createElement('td');
            if ((idx === 0 && i < firstDay) || day > lastDay) {
              td.textContent = '';
            } else {
              td.textContent = day++;
            }

            week.append(td);
          }

          acc.push(week);
          return acc;
        }, []);
      document.getElementsByClassName('calendar-body')[0].append(...weeks);
    }
  }

  window.CalrendarTodo = CalrendarTodo;
})();
